export const getRepos = async (): Promise<
  IServerResponse<{ repositories: IRepository[] }>
> => {
  await new Promise(resolve => setTimeout(resolve, 200));

  return {
    messages: [],
    payload: {
      repositories: serverRepoList
    }
  };
};

export const createRepo = async (
  repo: Overwrite<IRepository, { id?: number }>
): Promise<IServerResponse<IRepository>> => {
  await new Promise(resolve => setTimeout(resolve, 200));
  const newRepo = { ...repo, id: Math.random() };
  serverRepoList.push(newRepo);
  return { messages: [], payload: newRepo };
};
