import axios from "axios";
import { transformResponse } from ".";
import { IServerResponse, Overwrite } from "@/shared/types";
import { IJob } from "../modules/JobsModule";

const serverJobsList = [
  { name: "My daily job", id: 20 },
  { name: "Weekly backup", id: 21 },
  { name: "Important files", id: 22 }
];

export const getJobs = async (): Promise<IServerResponse<{ jobs: IJob[] }>> => {
  await new Promise(resolve => setTimeout(resolve, 200));

  return {
    messages: [],
    payload: {
      jobs: serverJobsList
    }
  };
};

export const createJob = async (
  repo: Overwrite<IJob, { id?: number }>
): Promise<IServerResponse<IJob>> => {
  await new Promise(resolve => setTimeout(resolve, 200));
  const newJob = { ...repo, id: Math.random() };
  serverJobsList.push(newJob);
  return { messages: [], payload: newJob };
};
