// import "@babel/polyfill";
import "babel-polyfill";

import Vue from "vue";
import store from "./store";
// init dynamic modules after store init
import "@/store/modules";
// init watchers after modules
import "@/store/globalWatchers";
import "./plugins/vuetify";
import "./plugins/meta";
import "./plugins/misc";
import i18n from "./plugins/i18n";
import App from "./App.vue";
import router from "./router";
import "roboto-fontface/css/roboto/roboto-fontface.css";
import "@mdi/font/css/materialdesignicons.css";
import { sync } from "vuex-enhanced-router-sync";

sync(store, router, { immutable: false, moduleName: "_internalRoute" }); // this is used only by RouterModule

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount("#app");
