import store from "@/store";
import { Module, VuexModule, Action, Mutation } from "vuex-module-decorators";
import Vue from "vue";
import { AxiosResponse, AxiosError } from "axios";
import { IServerResponse, MessageType } from "@/shared/types";

@Module({ dynamic: true, store, name: "notifications", namespaced: true })
export default class SettingsModule extends VuexModule {
  private notifications: INotification[] = [];

  get notificationHistory() {
    return [...this.notifications].reverse();
  }
  get currentNotification(): INotification | false {
    const unseenNotifications = this.notifications.filter(n => !n.seen);
    return unseenNotifications[0] || false;
  }

  @Mutation
  closeCurrent() {
    for (let i = 0; i < this.notifications.length; i++) {
      if (!this.notifications[i].seen) {
        Vue.set(this.notifications[i], "seen", true);
        this.notifications = this.notifications; // temporary
        return;
      }
    }
    this.notifications = this.notifications; // temporary
  }

  @Mutation
  addFromApiResponse(
    data:
      | AxiosResponse<IServerResponse>
      | AxiosError
      | IServerResponse
      | Partial<INotification>[]
      | Partial<INotification>
  ) {
    let messages: INotification[] = [];
    const anyData: any = data;
    if (isAxiosServerResponse(data)) {
      messages = data.data.messages.map(m => ({
        type: m.type,
        name: m.name,
        args: m.args,
        seen: false,
        key: Math.random()
      }));
    } else if (
      isAxiosError(data) &&
      isAxiosServerResponse(data.response) &&
      isServerResponse(data.response.data)
    ) {
      messages = data.response.data.messages.map(m => ({
        type: m.type,
        name: m.name,
        args: m.args,
        seen: false,
        key: Math.random()
      }));
    } else if (isServerResponse(data)) {
      messages = data.messages.map(m => ({
        type: m.type,
        name: m.name,
        args: m.args,
        seen: false,
        key: Math.random()
      }));
    } else if (isNotificationArray(data)) {
      messages = data.map(m => ({
        type: m.type,
        name: m.name,
        args: m.args,
        seen: false,
        key: Math.random()
      }));
    } else if (
      anyData &&
      anyData.config &&
      anyData.config.data &&
      anyData instanceof Error
    ) {
      messages = [
        {
          type: MessageType.error,
          name: "api.connection-not-available",
          seen: false,
          key: Math.random()
        }
      ];
    } else if (isNotification(data)) {
      messages = [
        {
          type: anyData.type,
          seen: false,
          key: Math.random(),
          ...(anyData as INotification)
        }
      ];
    } else {
      console.warn("Called addNotification with data", data);
    }
    if (messages && messages.length) {
      for (const message of messages) {
        this.notifications.push(message);
        // limit notifications to 50
        if (this.notifications.length > 50) {
          this.notifications.shift();
        }
        this.notifications = this.notifications;
      }
    }
  }
}

export interface INotification {
  type: MessageType;
  name: string;
  args?: { [index: string]: string };
  seen: boolean;
  key: number;
}

export const isAxiosServerResponse = (
  data: any
): data is AxiosResponse<IServerResponse> =>
  !!(data && data.config && data.headers && data.request && data.data);

export const isAxiosError = (data: any): data is AxiosError =>
  !!(
    data &&
    data.response &&
    data.response.data &&
    data.config &&
    data.code &&
    data.response &&
    data.stack
  );

export const isServerResponse = (data: any): data is IServerResponse =>
  !!(data && data.messages && Array.isArray(data.messages));

export const isNotification = (data: any): data is INotification =>
  !!(data && data.name && data.type);

export const isNotificationArray = (data: any): data is INotification[] =>
  !!(
    data &&
    Array.isArray(data) &&
    data.reduce<boolean>((prev, cur) => prev && isNotification(cur), true)
  );
