



















import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";
import { notificationModule, settingsModule } from "@/store/modules";

@Component({})
export default class AppComponent extends Mixins() {
  snackbarShown = false;
  get currentNotification() {
    if (!settingsModule.showSnackbar) {
      return false;
    }
    return notificationModule.currentNotification;
  }

  @Watch("snackbarShown")
  onSnackbarShowChange() {
    if (!this.snackbarShown) {
      this.$nextTick(() => {
        notificationModule.closeCurrent();
      });
    }
  }

  @Watch("currentNotification", { immediate: true })
  onCurrentNotificationChange() {
    if (this.currentNotification) {
      this.snackbarShown = true;
    }
  }
}
