import store from ".";
import {
  authModule,
  repositoriesModule,
  jobsModule,
  settingsModule
} from "./modules";

// fetch jobs, settings and repos when user is logged in
store.watch(
  () => ({
    isLoggedIn: authModule.isLoggedIn
  }),
  async ({ isLoggedIn }) => {
    if (isLoggedIn) {
      await Promise.all([
        repositoriesModule.fetchAllRepositories(),
        jobsModule.fetchAllJobs(),
        settingsModule.fetchAllSettings()
      ]);
    }
  },
  { immediate: true }
);

store.watch(
  () => ({
    settings: settingsModule.allSettings
  }),
  async ({ settings }) => {
    console.log("settings updated");
    await settingsModule.saveAllSettings();
  },
  { deep: true, immediate: false }
);

// use a global variable as hot reloading won't clear the interval automatically
clearTimeout(window._PING_TIMEOUT);
const ping = async () => {
  await authModule.pingServer();
  let timeout = 10000;
  if (!authModule.isServerReachable) {
    timeout = 2000;
  }
  window._PING_TIMEOUT = setTimeout(ping, timeout);
};
ping();
