



























































































































import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";
import {
  repositoriesModule,
  jobsModule,
  notificationModule,
  authModule
} from "@/store/modules";
import TheSnackbar from "./TheSnackbar.vue";

@Component({
  components: {
    TheSnackbar
  }
})
export default class TheHeaderComponent extends Mixins() {
  dialog = false;
  drawer: null | boolean = null;
  notificationDrawer = false;
  searchInput = "";
  sidebarModels = [];

  async performLogout() {
    await authModule.logout();
    this.$router.push("/login");
  }

  startSearch() {
    this.$router.push({
      path: "/search",
      query: { query: this.searchInput }
    });
  }

  get layout() {
    return this.$route.meta.layout || "default";
  }
  get isServerReachable() {
    return authModule.isServerReachable;
  }
  get recentlyUsedRepositories() {
    return repositoriesModule.recentlyUsed;
  }
  get recentlyUsedJobs() {
    return jobsModule.recentlyUsed;
  }
  get notificationHistory() {
    return notificationModule.notificationHistory;
  }

  get items() {
    return [
      { heading: this.$t("navigation.general") },
      { icon: "mdi-home", text: this.$t("navigation.home"), to: "/" },
      {
        icon: "mdi-history",
        text: this.$t("navigation.events"),
        to: "/events"
      },
      {
        icon: "mdi-chevron-up",
        "icon-alt": "mdi-chevron-down",
        text: this.$t("navigation.repositories"),
        children: [
          {
            icon: "mdi-nas",
            text: this.$t("navigation.all-repositories"),
            to: "/repositories",
            exact: true
          },
          {
            icon: "mdi-plus",
            text: this.$t("navigation.create-repository"),
            to: "/repositories/new"
          },
          ...this.recentlyUsedRepositories.map(repo => ({
            text: repo.name,
            to: `/repositories/${repo.id}`
          }))
        ]
      },
      {
        icon: "mdi-chevron-up",
        "icon-alt": "mdi-chevron-down",
        text: this.$t("navigation.jobs"),
        children: [
          {
            icon: "mdi-calendar-check",
            text: this.$t("navigation.all-jobs"),
            to: "/jobs",
            exact: true
          },
          {
            icon: "mdi-plus",
            text: this.$t("navigation.create-job"),
            to: "/jobs/new"
          },
          ...this.recentlyUsedJobs.map(job => ({
            text: job.name,
            to: `/jobs/${job.id}`
          }))
        ]
      },
      { heading: this.$t("navigation.other") },
      {
        icon: "mdi-settings",
        text: this.$t("navigation.settings"),
        to: "/settings"
      },
      { icon: "mdi-help", text: this.$t("navigation.help"), to: "/help" }
    ];
  }
}
