import axios from "axios";
import { transformResponse } from ".";
import {
  ISettingsModule,
  Language,
  ReportLanguage
} from "../modules/SettingsModule";
import { IServerResponse } from "@/shared/types";

export enum UpdateCheckInterval {
  HOURLY = "HOURLY",
  DAILY = "DAILY",
  WEEKLY = "WEEKLY"
}

export enum EmailNotificationType {
  ALWAYS = "ALWAYS",
  ON_ERROR = "ON_ERROR",
  NEVER = "NEVER"
}

let serverSettings: ISettingsModule = {
  gui: {
    language: "en" as Language,
    showSnackbar: true,
    theme: {
      dark: true,
      primary: "#456CB2",
      secondary: "#424242",
      accent: "#03CC00"
    }
  },
  userData: {
    email: "",
    firstName: "",
    lastName: "",
    username: "user"
  },
  notification: {
    from: "",
    hostname: "",
    password: "",
    port: 0,
    username: "",
    recipient: [] as string[],
    reportLanguage: "gui" as ReportLanguage,
    sendEmails: true
  },
  general: {
    automaticUpdates: true,
    enableRegister: false,
    updateCheckInterval: UpdateCheckInterval.DAILY
  },
  backup: {
    defaultEmailNotification: EmailNotificationType.ALWAYS,
    defaultMaxBackupsPerRepo: 999,
    defaultUnlockTime: 60 * 5 // 5 hours (time in minutes)
  }
};

export const getSettings = async (): Promise<
  IServerResponse<ISettingsModule>
> => {
  await new Promise(resolve => setTimeout(resolve, 200));

  return {
    messages: [],
    payload: serverSettings
  };
};

export const saveSettings = async (
  settings: ISettingsModule
): Promise<IServerResponse<ISettingsModule>> => {
  serverSettings = settings;
  return getSettings();
};
