import axios, { AxiosError } from "axios";
import { authModule, notificationModule } from "../modules";

export const baseUrl =
  process.env.NODE_ENV === "development"
    ? "http://localhost:8380/api/"
    : "/api/";

export const axiosInstance = axios.create({
  baseURL: baseUrl,
  responseType: "json"
});

axiosInstance.interceptors.request.use(
  config => {
    // success request interceptor
    return {
      ...config,
      headers: {
        ...config.headers,
        common: {
          ...config.headers.common,
          // add authorization header for authentication
          Authorization: authModule.authHeader
        }
      }
    };
  },
  error => {
    // error request interceptor
    console.log("error request");
    return Promise.reject(error);
  }
);

axiosInstance.interceptors.response.use(
  response => {
    // success response interceptor
    notificationModule.addFromApiResponse(response);
    return { ...response };
  },
  (error: AxiosError) => {
    // error response interceptor

    if (!error.response) {
      // the error was a network error => the server is probably down
      // calls to addFromApiResponse were commented out because this is handled
      // elsewhere
      // TODO: maybe return a response to the caller about the error type
      // if (error instanceof Error && error.message === "Network Error") {
      //   notificationModule.addFromApiResponse({
      //     type: MessageType.error,
      //     name: "api.error.host-not-reachable"
      //   });
      // } else {
      //   notificationModule.addFromApiResponse({
      //     type: MessageType.error,
      //     name: "api.error.other",
      //     args: {
      //       error: error.name
      //     }
      //   });
      // }
    } else {
      notificationModule.addFromApiResponse(error.response);
    }
    return Promise.reject(error);
  }
);
