import { getModule } from "vuex-module-decorators";
import SettingsModule from "./SettingsModule";
import NotificationModule from "./NotificationModule";
import AuthModule from "./AuthModule";
import JobsModule from "./JobsModule";
import RepositoriesModule from "./RepositoriesModule";
import RouterModule from "./RouterModule";
import { ADD_STORE_MODULES_TO_WINDOW } from "@/constants";

export const settingsModule = getModule(SettingsModule);
export const notificationModule = getModule(NotificationModule);
export const authModule = getModule(AuthModule);
export const jobsModule = getModule(JobsModule);
export const repositoriesModule = getModule(RepositoriesModule);
export const routerModule = getModule(RouterModule);

// give access to store in dev mode
if (ADD_STORE_MODULES_TO_WINDOW) {
  Object.assign(window, {
    settingsModule,
    notificationModule,
    authModule,
    jobsModule,
    repositoriesModule,
    routerModule
  });
}
