import { transformResponse } from ".";
import { SKIP_USER_EXISTS_CHECK } from "@/constants";

import {
  ILoginArgs,
  ILoginResponseSuccess,
  IServerResponse,
  ICreateUserArgs,
  IAnyExistsResponseSuccess
} from "@/shared/types";
import { axiosInstance } from "./axios";

export const login = async (args: ILoginArgs) => {
  const response = await axiosInstance.post<
    IServerResponse<ILoginResponseSuccess>
  >("/user/login", args);
  return transformResponse(response);
};

export const logout = async () => {
  // TODO
  const response = await axiosInstance.post<IServerResponse>("/users/logout");
  return transformResponse(response);
};

export const createUser = async (args: ICreateUserArgs) => {
  const response = await axiosInstance.post<IServerResponse>(
    "/user/register",
    args
  );
  return transformResponse(response);
};

export const anyUserExists = async (): Promise<
  IServerResponse<IAnyExistsResponseSuccess>
> => {
  if (SKIP_USER_EXISTS_CHECK) {
    return { payload: { value: true }, messages: [] };
  }
  const response = await axiosInstance.get<
    IServerResponse<IAnyExistsResponseSuccess>
  >("/user/anyExists");
  return transformResponse(response);
};
