import store from "@/store";
import { Module, VuexModule, Action, Mutation } from "vuex-module-decorators";
import Vue from "vue";
import { AxiosResponse, AxiosError } from "axios";
import * as api from "../api";
import { Overwrite } from "@/shared/types";
@Module({ dynamic: true, store, name: "repositories", namespaced: true })
export default class RepositoriesModule extends VuexModule {
  private repositories: IRepository[] = [];

  @Action
  async fetchAllRepositories() {
    const response = await api.getRepos();
    if (response.payload) {
      this.setRepositories(response.payload.repositories);
    }
  }

  @Action
  async createRepository(
    repository: Overwrite<
      IRepository,
      {
        id?: IRepository["id"];
      }
    > // TODO: maybe make more fields optional
  ) {
    // TODO: replace with api call
    const resp = await api.createRepo(repository);
    if (resp.payload) {
      this.setRepositories([...this.allRepositories, resp.payload]);
      return resp;
    }
  }

  @Mutation
  setRepositories(repos: IRepository[]) {
    this.repositories = repos;
  }

  get recentlyUsed() {
    // return at most 5 repositories (shown in side menu)
    return this.repositories.slice(0, 5);
  }
  get allRepositories() {
    return this.repositories;
  }

  get getById() {
    return (id: string | number) => {
      const num = Number(id);
      const repo = this.allRepositories.find(r => Number(r.id) === num);
      return repo;
    };
  }
}

export interface IRepository {
  name: string;
  password: string;
  location: string;
  s3AccessKey: string;
  s3SecretKey: string;
  id: number;
}
